.lap{
    display: flex;
    justify-content: space-between;
    margin: 5px;
    border-radius: 15px;
    background-color: #d8d8d8;
}

.lapTitleAndIndex{
    background-color: rgb(125, 178, 221);
    
    border-radius: 15px;
}

.lapTitle{
    margin: 0 5px 0 10px;
}

.lapIndex{
    background-color: rgb(49 112 140);
    /* padding-right: 10px; */
    border-radius: 15px;
    color: white;
    padding: 0 6px;
}

.lapTime{
    padding: 0 5px;
}

.lapDifference{
    padding: 0 5px;
}

.slowerLap{
    background-color: rgb(255 75 75 / .5);
    border-radius: 15px;
}

.fasterLap{
    background-color: rgb(0 255 55 / .5);
    border-radius: 15px;
}


.lap.firstLap{
    justify-content: left;
}

.timeAndDiff{
    justify-content: space-between;
}